import { FunctionalComponent, h, RefObject } from 'preact';
import { forwardRef } from 'preact/compat';
import Settings from 'src/core/components/Header/components/Settings';
import { Animated } from 'src/shared/components/Animated';
import { DropPortal } from 'src/shared/components/DropPortal/DropPortal';
import styles from './styles.module.scss';

interface Props {
  open: boolean;
  targetRef: RefObject<HTMLElement>;
}

export const SettingsDropdown: FunctionalComponent<Props> = forwardRef<HTMLElement, Props>(
  ({ open, targetRef }, ref) => {
    return (
      <DropPortal targetRef={targetRef} position={{ left: -320, top: 50 }}>
        <Animated render={open}>
          <div class={styles['settings-dropdown']} ref={ref as RefObject<HTMLDivElement>}>
            <Settings />
          </div>
        </Animated>
      </DropPortal>
    );
  }
);
