import { FunctionalComponent, h, RefObject } from 'preact';
import { forwardRef, StateUpdater, useLayoutEffect } from 'preact/compat';
import { useRef } from 'preact/hooks';
import { Animated } from 'src/shared/components/Animated';
import { DropPortal } from 'src/shared/components/DropPortal/DropPortal';
import Settings from 'src/core/components/Header/components/Settings';
import MobileNav from 'src/core/components/Header/components/MobileNav';

import styles from 'src/core/components/Header/components/MobileMenu/style.module.scss';

interface Props {
  open: boolean;
  openMenu: StateUpdater<boolean>;
}

export const MobileMenu: FunctionalComponent<Props> = forwardRef<HTMLElement, Props>(
  ({ open, openMenu }, ref) => {
    const targetRef = useRef();

    useLayoutEffect(() => {
      targetRef.current = document.getElementById('main-header');
    });

    return (
      <DropPortal targetRef={targetRef as RefObject<HTMLElement>} position={{ left: 'fullwidth' }}>
        <Animated render={open}>
          <div class={styles['mobile-menu']} ref={ref as RefObject<HTMLDivElement>}>
            <MobileNav openMenu={openMenu} />
            <Settings />
          </div>
        </Animated>
      </DropPortal>
    );
  }
);
