import { FunctionalComponent, h } from 'preact';

interface Props {
  href: string;
  class?: string;
}

export const ExternalLink: FunctionalComponent<Props> = props => {
  return (
    <a href={props.href} class={props.class} target="_blank" rel="noopener noreferrer">
      {props.children}
    </a>
  );
};
