import { FunctionalComponent, h, RefObject } from 'preact';
import { createPortal } from 'preact/compat';
import { Rect, useRect } from 'src/core/hooks/useRect';
import { portalsRoot } from 'src/shared/PortalsRoot';

import styles from 'src/shared/components/DropPortal/styles.module.scss';

interface CSSPosition {
  left?: `${number}px` | 0;
  right?: `${number}px` | 0;
  top?: `${number}px` | 0;
  bottom?: `${number}px` | 0;
}

interface Props {
  targetRef: RefObject<HTMLElement>;
  position?: {
    left?: number | 'fullwidth';
    top?: number;
  };
}

export const DropPortal: FunctionalComponent<Props> = ({ targetRef, position, children }) => {
  const rect: Partial<Rect> = useRect(targetRef);

  const rectTop =
    rect?.top !== undefined && rect?.height !== undefined ? rect.top + rect.height : rect?.top || 0;
  const rectLeft = rect?.left || 0;

  const style: Partial<CSSPosition> = {
    top: `${position?.top ? position.top + rectTop : rectTop}px`
  };

  if (position?.left === 'fullwidth') {
    style.left = 0;
    style.right = 0;
  } else {
    style.left = `${position?.left ? position.left + rectLeft : rectLeft}px`;
  }

  return createPortal(
    <div class={styles['drop-portal']} style={style}>
      {children}
    </div>,
    portalsRoot()
  );
};
