import { FunctionalComponent, h } from 'preact';
import { useContext } from 'preact/compat';
import { languages } from 'src/core/components/Header/components/Settings/components/LanguagesToggle/constants';
import { ThemeToggle } from 'src/core/components/Header/components/Settings/components/ThemeToggle';
import { ThemeContext } from 'src/core/containers';
import LanguageContext from 'src/core/containers/language-provider/language-context';
import { THEME } from 'src/core/models';
import { ExternalLink } from 'src/shared/components/ExternalLink';
import { Text } from 'preact-i18n';
import style from './style.module.scss';
import { ExternalLinks } from 'src/shared/Constants';

interface Props {
  setMode(mode: 'default' | 'languages'): void;
}

const arrowImg = {
  [THEME.LIGHT]: 'assets/icons/arrow_light.svg',
  [THEME.DARK]: 'assets/icons/arrow.svg'
};

const SettingsMenu: FunctionalComponent<Props> = ({ setMode }) => {
  const { theme, toggleTheme } = useContext(ThemeContext);
  const { language } = useContext(LanguageContext);

  const languageInfo = languages.find(lng => lng.id === language)!!;

  return (
    <div class={style.settings}>
      <h3 className={style['settings__label']}>
        <Text id="nav.settings">Settings</Text>
      </h3>

      <ul class={style['settings__list']}>
        <li class={style['settings__item']} onClick={toggleTheme}>
          <div class={style['settings__header']}>
            <img class={style['settings__icon']} src="assets/icons/theme.svg" alt="theme" />
            <ThemeToggle />
          </div>

          <div>
            <h4 className={style['settings__title']}>
              <Text id="settings.darkMode">Dark mode</Text>
            </h4>
            <p className={style['settings__text']}>
              <Text id="settings.themeForTheWeb">Theme for the web</Text>
            </p>
          </div>
        </li>

        <li className={style['settings__item']} onClick={() => setMode('languages')}>
          <div className={style['settings__header']}>
            <img
              height="32"
              width="32"
              className={style['settings__icon']}
              src={languageInfo.image}
              alt="Language flag"
            />
            <img src={arrowImg[theme]} alt="Change language" />
          </div>

          <div>
            <h4 className={style['settings__title']}>{languageInfo.title}</h4>
            <p className={style['settings__text']}>
              <Text id="settings.chooseLanguage">Choose language</Text>
            </p>
          </div>
        </li>

        <li class={style.settings__item}>
          <ExternalLink class={style['settings__item-container']} href={ExternalLinks.tutorials}>
            <div className={style['settings__header']}>
              <img
                className={style['settings__icon']}
                src="assets/icons/tutorials.svg"
                alt="Tutorials"
              />
              <img
                className={style['settings__icon']}
                src={arrowImg[theme]}
                alt="Go to tutorials"
              />
            </div>

            <div>
              <h4 className={style['settings__title']}>
                <Text id="settings.tutorials">Tutorials</Text>
              </h4>
              <p className={style['settings__text']}>
                <Text id="settings.howToUse">How to use</Text>
              </p>
            </div>
          </ExternalLink>
        </li>
      </ul>
    </div>
  );
};

export default SettingsMenu;
