import { FunctionalComponent, h } from 'preact';
import { useContext } from 'preact/compat';
import { ThemeContext } from 'src/core/containers';
import { THEME } from 'src/core/models';

import styles from 'src/core/components/Header/components/Settings/components/ThemeToggle/styles.module.scss';

export const ThemeToggle: FunctionalComponent = () => {
  const { theme, toggleTheme } = useContext(ThemeContext);

  return (
    <label class={styles['theme-toggle-button']}>
      <input type="checkbox" checked={theme === THEME.DARK} onChange={toggleTheme} />
      <span class={styles.slider} />
    </label>
  );
};
