import { FunctionalComponent, h } from 'preact';
import { Link } from 'preact-router/match';
import { Text } from 'preact-i18n';
import { useContext } from 'preact/compat';
import { ExternalLinks } from 'src/shared/Constants';
import style from './style.module.scss';
import Burger from './components/Burger';
import { THEME } from '../../models';
import { ThemeContext } from '../../containers';

const logoImg = {
  [THEME.LIGHT]: 'assets/icons/logo_dark.svg',
  [THEME.DARK]: 'assets/icons/logo.svg'
};

const Header: FunctionalComponent = () => {
  const { theme } = useContext(ThemeContext);

  return (
    <header id="main-header" class={style.header}>
      <div class={style['header__container']}>
        <Link class={style['header__logo']} href="/">
          <img src={logoImg[theme]} alt="Rubic logo" />
        </Link>

        <nav class={style['header__nav']}>
          <a href={ExternalLinks.team} target="_blank" rel="noreferrer">
            <Text id="nav.team">Team</Text>
          </a>
          <a href={ExternalLinks.fiatOnRamp} target="_blank" rel="noreferrer">
            <Text id="nav.fiatOnRamp">Fiat On-ramp</Text>
          </a>
          <a href={ExternalLinks.faq} target="_blank" rel="noreferrer">
            <Text id="nav.faq">FAQ</Text>
          </a>
        </nav>

        <div class={style['header__burger-container']}>
          <Burger />

          <a
            href={ExternalLinks.app}
            class={style['header__launch']}
            target="_blank"
            rel="noreferrer"
          >
            <Text id="header.launchApp">Launch app</Text>
          </a>
        </div>
      </div>
    </header>
  );
};

export default Header;
