import { useWindowWidth } from '@react-hook/window-size';
import { FunctionalComponent, h, Ref, RefObject } from 'preact';
import { useCallback, useRef, useState } from 'preact/hooks';
import { SettingsDropdown } from 'src/core/components/Header/components/SettingsDropdown';
import { MobileMenu } from 'src/core/components/Header/components/MobileMenu';
import { useClickOutside } from 'src/core/hooks/useClickOutside';
import cn from 'classnames';
import { Constants } from 'src/shared/Constants';

import style from 'src/core/components/Header/components/Burger/style.module.scss';

const Burger: FunctionalComponent = () => {
  const [open, setOpen] = useState(false);
  const ref: RefObject<HTMLElement> = useRef() as RefObject<HTMLElement>;
  const dropDownRef: RefObject<HTMLElement> = useRef() as RefObject<HTMLElement>;

  const closeOnClickOutside = useCallback(() => setOpen(false), [setOpen]);
  useClickOutside(dropDownRef, closeOnClickOutside, [ref]);

  const windowWidth = useWindowWidth();
  const isTablet = windowWidth <= Constants.tabletWidth;

  return (
    <div class={style.burger}>
      <button class={style.hamb} onClick={() => setOpen(!open)} ref={ref as Ref<HTMLButtonElement>}>
        <div class={cn(style.hamb__item, { [style.hamb__item_active]: open })} />
        <div class={cn(style.hamb__item, { [style.hamb__item_active]: open })} />
        <div class={cn(style.hamb__item, { [style.hamb__item_active]: open })} />
      </button>
      {isTablet ? (
        <MobileMenu open={open} openMenu={setOpen} ref={dropDownRef} />
      ) : (
        <SettingsDropdown open={open} targetRef={ref} ref={dropDownRef} />
      )}
    </div>
  );
};

export default Burger;
