import { FunctionalComponent, h } from 'preact';
import { useState } from 'preact/hooks';
import { LanguagesToggle } from 'src/core/components/Header/components/Settings/components/LanguagesToggle';
import SettingsMenu from './Settings';

const Settings: FunctionalComponent = () => {
  const [currentMode, setCurrentMode] = useState<'default' | 'languages'>('default');

  const components = {
    default: <SettingsMenu setMode={setCurrentMode} />,
    languages: <LanguagesToggle onBackClick={() => setCurrentMode('default')} />
  };

  return components[currentMode];
};

export default Settings;
