import { Attributes, cloneElement, Fragment, FunctionalComponent, h, VNode } from 'preact';
import { useEffect, useState } from 'preact/hooks';
import { useDelayUnmount } from 'src/core/hooks/useDelayUnmount';
import cn from 'classnames';

import styles from 'src/shared/components/Animated/styles.module.scss';

interface Props {
  render: boolean;
}

const combineClassNames = (children: { props: { class?: Attributes[] } }) => {
  return cn(children.props.class, styles.animated);
};

export const Animated: FunctionalComponent<Props> = ({ render, children }) => {
  const shouldRenderChild = useDelayUnmount(render, 300);
  const [mountedStyle, setMountedStyle] = useState({
    opacity: 0
  });
  const unmountedStyle = { opacity: 0 };

  useEffect(() => {
    let timeoutId: NodeJS.Timeout;
    if (render && shouldRenderChild) {
      timeoutId = setTimeout(() => {
        setMountedStyle({ opacity: 1 });
      }, 10);
    } else {
      setMountedStyle({ opacity: 0 });
    }
    return () => {
      clearTimeout(timeoutId);
    };
  }, [render, shouldRenderChild]);

  if (typeof children === 'object') {
    const node = Array.isArray(children) ? children[0] : children;
    const child = cloneElement(node as VNode, {
      style: render ? mountedStyle : unmountedStyle,
      class: combineClassNames(node as { props: object })
    });

    return <Fragment>{shouldRenderChild && child}</Fragment>;
  }

  return (
    <div class={styles.animated} style={render ? mountedStyle : unmountedStyle}>
      {shouldRenderChild && children}
    </div>
  );
};
