import { FunctionalComponent, h } from 'preact';
import { useContext } from 'preact/compat';
import cn from 'classnames';
import { Text } from 'preact-i18n';
import { languages } from 'src/core/components/Header/components/Settings/components/LanguagesToggle/constants';
import { ThemeContext } from 'src/core/containers';
import LanguageContext from 'src/core/containers/language-provider/language-context';
import { THEME } from 'src/core/models';

import styles from './styles.module.scss';

interface Props {
  onBackClick: () => void;
}

const arrowImg = {
  [THEME.LIGHT]: 'assets/icons/arrow_light.svg',
  [THEME.DARK]: 'assets/icons/arrow.svg'
};

export const LanguagesToggle: FunctionalComponent<Props> = ({ onBackClick }) => {
  const { theme } = useContext(ThemeContext);
  const { language, setLanguage } = useContext(LanguageContext);

  return (
    <div class={styles.container}>
      <div class={styles.container__header}>
        <button className={styles.container__back} onClick={onBackClick}>
          <img src={arrowImg[theme]} alt="Back" />
        </button>
        <h3>
          <Text id="settings.languages">Languages</Text>
        </h3>
      </div>
      <ul class={styles['container__languages-list']}>
        {languages.map(lang => (
          <li
            key={lang.id}
            class={cn(styles.container__language, {
              [styles.container__language_active]: lang.id === language
            })}
            onClick={() => {
              setLanguage(lang.id);
              onBackClick();
            }}
          >
            <img src={lang.image} alt="Language symbol" />
            <span>{lang.title}</span>
          </li>
        ))}
      </ul>
    </div>
  );
};
